import Vue from 'vue'
import VueRouter from 'vue-router'
// import Home from '../views/Home.vue'
import Pacienti from '../views/Pacienti.vue'
import Istoric from '../views/Istoric.vue'
import Fisa from '../components/Fisa.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Fisa
  },
  {
    path: '/fisa',
    name: 'Fisa',
    component: Fisa
  },
  {
    path: '/pacienti',
    name: 'Pacienti',
    component: Pacienti
  },
  {
    path: '/istoric',
    name: 'Istoric',
    component: Istoric
  },
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
